function stringReverse(inputString) {
  const myStringArray = inputString.split("");
  // console.log(myStringArray);
  const reversedArray = [];
  for (let i = myStringArray.length; i >= 0; i--) {
    // console.log(myStringArray[i]);
    reversedArray.push(myStringArray[i]);
  }
  console.log(reversedArray.join(""));
  return reversedArray.join("");
}

stringReverse("hippo");

